/* ==========================================================================
   BLOCK OBJECT
   ========================================================================== */
/**
* Stacked image-with-text object. A simple abstraction to cover a very commonly
* occurring design pattern.
*/
.o-block {
  display: block;
  text-align: center;
}
.o-block__img {
  margin-bottom: 2em;
  /* Size variants.
        ===================================================================== */
}
.o-block--flush > .o-block__img {
    margin-bottom: 0;
}
.o-block--tiny > .o-block__img {
    margin-bottom: 0.66667em;
}
.o-block--small > .o-block__img {
    margin-bottom: 1em;
}
.o-block--large > .o-block__img {
    margin-bottom: 4em;
}
.o-block--huge > .o-block__img {
    margin-bottom: 6em;
}
.o-block__body {
  display: block;
}

/* Alignment variants.
========================================================================== */
.o-block--right {
  text-align: right;
}
.o-block--left {
  text-align: left;
}



