/* ==========================================================================
   MEDIA OBJECT
   ========================================================================== */
/**
 * Place any image- and text-like content side-by-side, as per:
 * https://goo.gl/MDV4HS
 *
 * The Flexbox version of the Media Objects allows it to behave as a flag,
 * placing the image both at the bottom or the center of the whole element.
 *
 * 1. Image vertically aligned at top by default.
 */
.o-media {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  /* [1] */
}
.o-media__img {
  margin-right: 2em;
}
.o-media__img > img {
    display: block;
}
.o-media__body {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.o-media__body,
  .o-media__body > :last-child {
    margin-bottom: 0;
}

/* Alignment variants.
========================================================================== */
/**
* Vertically align the image- and body-content differently.
*/
.o-media--center {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.o-media--bottom {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}

/**
